@import "../../global.scss";

.topbar {
    width: 100%;
    height: 70px;
    background-color: white;
    color: $maincolor;
    position: fixed;
    top: 0;
    z-index: 3;
    transition: all 1s ease;
    overflow: hidden;


    .wrapper{
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    
    .logo{
        font-size:40px;
        font-weight: 700;
        text-decoration: none;
        color: inherit;
    }
    
    .right{
        .hamburger{
            width: 35px;
            height: 29px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            cursor: pointer;
            
    
            span{
                width: 100%;
                height: 3px;
                background-color: $maincolor;
                transform-origin: left;
                transition: all 1s ease;
                scroll-behavior: none;

            }
        }
    }

    &.active{
        background-color: $maincolor;
        color: white;

        .hamburger{
            span{
                &:first-child{
                    background-color: white;
                    transform: rotate(45deg);
                }
                &:nth-child(2){
                    opacity:0;
                }
                &:last-child{
                    background-color: white;
                    transform: rotate(-45deg);

                }
                
            }
        }
    }
}




